.card {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.order {
  text-shadow: 0px 0px 16px rgba(51, 51, 255, 0.25),
    0px 0px 8px rgba(51, 51, 255, 0.25), 0px 4px 32px rgba(51, 51, 255, 0.5);
}

.burgerName {
  composes: card;
  text-align: center;
}

.span {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.icon {
  width: 30%;
}